export default {
	components:{
		UniNavBar,
		UniBackground,
		FunButton
	},
	data() {
		return {
			scroll:0,
			showModal:false,
			checkCert:true,
			navButtons:{
				back:{
					type:'normal',
					text:'取消'
				}
			},
			imageLib:{
				info:'../../static/icons/icon_info.png',
